import {Col, Container, Row} from "react-bootstrap";

function Banner(){

    return(
        <>
            <Container fluid>
                <Row>
                    <Col xl={12} lg={12} md={12} xs={12} sm={12} >
                    <div className="banner-flex-container">
                        <div className="banner-content">
                            <div className="banner-heading">
                            <h2>Lets try our service now!</h2>
                            </div>
                            <div className="banner-paragraph">
                            <p>Everything you need to accept card payments<br/>and grow your business anywhere on the planet.</p>
                            </div>
                        </div>
                        <div className="banner-button banner-content">
                            <button className="">Get Started</button>
                        </div>
                    </div>

                    </Col>
                </Row>
            </Container>
        </>
    );
}
export default Banner;