import { Container, Row, Col } from 'react-bootstrap';
import { logo } from '../assets';
import { useState, useEffect } from 'react';

function Navbar() {
    const [toggle, setToggle] = useState(true);

    useEffect(() => {
        const handleResize = () => {
            // Check the screen size and reset toggle if it's medium
            if (window.innerWidth >= 768 && window.innerWidth < 992) {
                setToggle(true);
            }
        };

        // Add event listener for window resize
        window.addEventListener('resize', handleResize);

        // Clean up the event listener on component unmount
        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []); // Empty dependency array ensures the effect runs only once after mount

    return (
        <>
            <Container fluid>
            <Row>
                <Col md={12} xs={12} sm={12} lg={12} xl={12}>
                    <div className="navbar-container">
                        {/* Logo */}
                        <div className="logo-div">
                            <img src={logo} alt="" className="logo" />
                        </div>
                        {/* Navbar */}
                        <div className="topnav-div d-md-block d-none">
                            <ul className="topnav">
                                <li>Home</li>
                                <li>Feature</li>
                                <li>Product</li>
                                <li>Clients</li>
                            </ul>
                        </div>
                        {/* Hamburger icon */}
                        <div className="hamburger-menu d-md-none position-fixed top-0 end-0 m-3">
                            {toggle ? (
                                <svg
                                    onClick={() => {
                                        setToggle(!toggle);
                                    }}
                                    viewBox="0 0 24 24"
                                    width="30"
                                    height="30"
                                    xmlns="http://www.w3.org/2000/svg"
                                >
                                    <path fill="white" d="M3 6h18v2H3zm0 5h18v2H3zm0 5h18v2H3z" />
                                </svg>
                            ) : (
                                <svg
                                    onClick={() => {
                                        setToggle(!toggle);
                                    }}
                                    viewBox="0 0 24 24"
                                    width="30"
                                    height="30"
                                    xmlns="http://www.w3.org/2000/svg"
                                >
                                    <path
                                        fill="white"
                                        d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"
                                    />
                                </svg>
                            )}
                        </div>
                        {/* Navbar for small devices */}
                        {!toggle && (
                            <div className="navbar-col-div d-md-none">
                                <ul className="navbar-col">
                                    <li>Home</li>
                                    <li>Feature</li>
                                    <li>Product</li>
                                    <li>Clients</li>
                                </ul>
                            </div>
                        )}
                    </div>
                </Col>
            </Row>
            </Container>
        </>
    );
}

export default Navbar;
