import {card} from "../assets";
import {Col, Container, Row} from "react-bootstrap";
function CardDeal(){
    return(
        <>
            <Container fluid>
            <Row>
                <Col md={6} xs={12} sm={12} lg={6} xl={6} className="">
                    <div className="card-box1">
                        <div className="card-heading">
                             <h2>Find a better card deal in few easy steps.</h2>
                        </div>
                    <div className="card-paragraph">
                        <p>Arcu tortor, purus in mattis at sed integer faucibus. Aliquet quis aliquet eget mauris tortor. Aliquet ultrices ac, ametau.</p>
                    </div>

                    <div className="card-button">
                        <button>Get Started</button>
                    </div>
                    </div>
                </Col>

                <Col md={6} xs={12} sm={12} lg={6} xl={6} className="">
                    <div className="card-box2">
                    <img src={card} alt="card"/>
                    </div>
                </Col>
            </Row>
            </Container>
        </>
    );
}
export default CardDeal;