// Company.js

import { Col, Container, Row } from "react-bootstrap";
import { airbnb, binance, coinbase, dropbox } from "../assets";

function Company() {
    return (
        <>
            <div className="company-main">
                <Container fluid>
                    <Row>
                        <Col className="text-center">
                            <img src={airbnb} alt="" className="img-fluid" />
                        </Col>

                        <Col className="text-center">
                            <img src={binance} alt="" className="img-fluid" />
                        </Col>

                        <Col className="text-center">
                            <img src={coinbase} alt="" className="img-fluid" />
                        </Col>

                        <Col className="text-center order-md-last">
                            <img src={dropbox} alt="" className="img-fluid" />
                        </Col>
                    </Row>
                </Container>
            </div>
        </>
    );
}

export default Company;
