import {Col, Container, Row} from "react-bootstrap";
import { discount, arrowUp, robot } from '../assets';

function Hero() {
    return (
        <>
            <Container fluid>
            <Row>
                <Col xl={6} lg={6} md={6} xs={12} className="order-md-1 order-xs-2">
                    <div className="section1-div">
                        <div className="bg-discount-gradient">
                            <p><img className="discount" src={discount} alt="discount" />20% Discount For 1 Month Account</p>
                        </div>


                        <div className="thetarget">

                        <h1 className="heading01">The Next <span className="gradient-text">Generation</span></h1>
                            <h1 className="heading1">Payment Method.</h1>


                            <div className="circle1 d-none d-xs-none d-sm-flex d-lg-flex d-md-inline-flex">
                            <div className="circle-content1">
                                <p className="gradient-text">Get <img src={arrowUp} alt="img" /> <br /> <span>Started</span></p>
                            </div>

                            </div>
                        </div>


                        <p>Our team of experts uses a methodology to identify credit cards most likely to fit your needs. we examine Annual percentage rate , Annual fees</p>
                    </div>
                </Col>


                <Col xl={6} lg={6} md={6} xs={12} className="order-md-2 order-xs-1 text-right">
                    <div className="robot text-right">
                        <img src={robot} alt="" />
                    </div>
                </Col>
            </Row>

            <Row>
            <Col xl={12} lg={12} md={12} xs={12} >
                <div className="circle2 d-xs-inline-flex d-sm-none d-lg-none d-md-none">
                    <div className="circle-content1">
                        <p className="gradient-text">Get <img src={arrowUp} alt="img" /> <br /> <span>Started</span></p>
                    </div>
                </div>
            </Col>
            </Row>
            </Container>
        </>
    );
}

export default Hero;
