import {Col, Container, Row} from "react-bootstrap";

function Headings(){

    return(
        <>
            <Container fluid>
            <Row>
                <Col xl={4} lg={4} md={4} sm={12} xs={12} >
                    <div className="heading2">
                <h4 className="">3800+</h4>
                    <p className="paragraph2 gradient-text">USER ACTIVE</p>
                    </div>
                </Col>

                <Col xl={4} lg={4} md={4} sm={12} xs={12} >
                    <div className="heading2">
                <h4 className="">230+</h4>
                    <p className="paragraph2 gradient-text">TRUSTED BY COMPANY</p>
                    </div>
                </Col>

                <Col xl={4} lg={4} md={4} sm={12} xs={12} >
                    <div className="heading2">
                <h4 className="">$230M+</h4>
                    <p className="paragraph2 gradient-text">TRANSACTION</p>
                    </div>
                </Col>
            </Row>
            </Container>
            <br/>
            <br/>
        </>
    );
}

export default Headings;